.bookList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  max-width: 1110px;
  margin: 20px auto;
}

.bookItem {
  display: flex;
  flex-direction: column;
  max-width: 150px;
  text-align: center;
  font-size: 0.8em;
  cursor: pointer;
}

.bookDetail {
  margin: 20px auto;
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap; 
}

.bookDetail__img {
  width: 200px;
  height: auto;
}
.bookDetail__info {
  max-width: 70vw;
  min-width: 400px;
}

.bookLink{
  border-bottom: 3px solid transparent;
}
.bookLink:hover{
  border-bottom: 3px solid #BF9130;
}

